body {
  overflow: hidden;
}

.app-container {
  background-color: #90a4ae;
  color: #000000;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
}

.title {
  font-size: 36px;
  font-weight: 900;
  text-align: center;
  color: #e9ff22;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin: 20px 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.button-container {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

.glow-button {
  border: none;
  outline: none;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 10px;
  background-color: transparent;
  color: #ffffff;
  cursor: pointer;
  position: relative;
  transition: background-color 0.5s ease-in-out;
}

.glow-button.start {
  background-color: #539b3b;
}

.glow-button.stop {
  background-color: #ff3d00;
  color: #ffffff;
  animation: pulse-shadow 1s infinite alternate;
}

.glow-button.reset {
  background-color: #745043;
  color: #ffffff;
}

.glow-button.copy {
  background-color: #546e7a;
  color: #ffffff;
}

.glow-button.lang {
  background-color: #40c4ff;
  color: #ffffff;
}

.glow-button.empty {
  background-color: #858c8f;
  color: #ffffff;
}

.transcript-container {
  margin-bottom: 20px;
}

.transcript-text {
  width: 80vw;
  height: 40vh;
  background-color: rgba(0, 0, 0, 0.1);
  border: none;
  outline: none;
  padding: 10px;
  color: #ffffff;
  font-size: 16px;
  font-family: "Courier New", monospace;
  resize: none;
}

.interim-transcript {
  font-size: 16px;
  color: #34ff4b;
}

.options-main {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.options-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px; /* Отступы между элементами */
  margin-top: 15px; /* Отступ сверху для списка */
}

.option-item {
  border: 1px solid #ccc; /* Граница элемента */
  padding: 10px;
  border-radius: 5px; /* Скругление углов */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Тень */
  background-color: #f8f8f8; /* Цвет фона */
}

.option-label {
  display: flex;
  align-items: center;
}

.option-checkbox {
  margin-right: 10px;
}

.toast {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.8);
  color: #ffffff;
  padding: 10px;
  border-radius: 5px;
  animation: fade-in-out 1s ease-in-out;
  position: 'absolute';
  left: '-9999px'
}

@keyframes pulse-shadow {
  0% {
    box-shadow: 0px 0px 10px rgba(255, 0, 0, 0.363);
  }
  100% {
    box-shadow: 0px 0px 20px rgba(255, 0, 0, 0.8);
  }
}

.language-toggle {
  position: absolute;
  top: 20px;
  right: 20px;
}

.language-selector {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  font-family: Arial, sans-serif;
}

.language-selector label {
  margin-right: 10px;
  font-weight: bold;
  color: #333;
}

.language-selector select {
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #ced9e6;
  cursor: pointer;
}

.language-selector select:focus {
  border-color: #90a4ae;
  outline: none;
}
